@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;
.rdp-root {
  --rdp-accent-color: #01b0cb; /* Change the accent color to indigo. */
  --rdp-accent-background-color: #dffbff; /* Change the accent background color. */
  --rdp-day_button-border-radius: 8px;
  --rdp-selected-font: bold medium var(--rdp-font-family);
}
/* Custom Scrollbar Styles */


@layer base {
  html {
    font-family: 'Roboto', sans-serif;
  }
  body {
    background-color: #f9f9f9;
    overflow-x: hidden;
  }
}
.scrollbar::-webkit-scrollbar-track {
  background-color: rgb(172, 201, 229); /* Change this to your desired color */
}
.scrollbar::-webkit-scrollbar-thumb {
  background-color:#057c8e; /* Change this to your desired color */
  border-radius: 3px;
}
.scrollbar::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}

@layer components {
  .input-box {
    @apply w-full text-sm bg-cyan-600/5 rounded px-5 py-3 mb-4 outline-none;
  }

  .btn-primary {
    @apply w-full text-sm font-medium text-white bg-cyan-500 shadow-lg shadow-cyan-200;
  }

  .btn-light {
    @apply bg-cyan-50 text-primary shadow-cyan-100/8 border border-cyan-100 hover:bg-cyan-100;
  }

  .login-ui-box {
    @apply w-80 h-[450px] rounded-full bg-primary absolute rotate-45;
  }

  .icon-btn {
    @apply text-[22px] text-slate-300 cursor-pointer hover:text-red-500;
  }

  .model-box {
    @apply w-[80vw] md:w-[40%] h-[80vh] bg-white rounded-lg mx-auto mt-14 p-5 overflow-y-scroll scrollbar z-50;
  }
  
  .btn-small {
    @apply flex items-center gap-1 text-xs font-medium bg-cyan-50 text-primary shadow-cyan-100/0 border border-cyan-100 hover:bg-primary hover:text-white rounded px-3 py-[3px];
  }
  .btn-delete{
    @apply bg-rose-50 text-rose-500 shadow-rose-100/0 border border-rose-100 hover:bg-rose-50 hover:text-white
  }
}
